.lucidity-flexbox-grid-system__collapsible h2 {
  display: flex;
  align-items: center;
  padding-top: 1em;
  padding-bottom: 1em;
}
.lucidity-flexbox-grid-system__collapsible h2 svg {
  width: auto;
  height: 1em;
  cursor: pointer;
  margin-left: auto;
}
.lucidity-flexbox-grid-system__collapsible h2,
.lucidity-flexbox-grid-system__collapsible hr {
  margin: 0;
}
.lucidity-flexbox-grid-system__collapsible__row {
  gap: 0.5em;
  display: flex;
  margin-bottom: 1.5em;
}
.lucidity-flexbox-grid-system__collapsible__col {
  flex: 1;
  display: flex;
}